import React from "react";
import Navbar from "../Components/Navbar/Navbar";
import Footer from "../Components/Footer/Footer";
import styled from "styled-components";
import JoinOurteam from "../Images/JoinOurTeam.png";

const JoinOurTeam = () => {
  return (
    <Container>
      <Navbar active="Company" />
      <Body>
        <Banner>
          <TextDiv>
            <h1>Join our team</h1>
            <h3>
              Our team is rapidly growing and we are currently seeking staff /
              Investors to help contribute to our success. If youre interested
              in more information, please reach out by sending us a mail.
            </h3>
            <h4>Giddyshopafrica@gmail.com</h4>
          </TextDiv>
          <BannerImg>
            <img src={JoinOurteam} alt="Join our Team" />
          </BannerImg>
        </Banner>
      </Body>
      <Footer />
    </Container>
  );
};
const Container = styled.div``;
const Body = styled.div`
  height: fit-content;
`;
const Banner = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px;
  padding-top: 15px;
  padding-bottom: 80px;
  margin-left: 20px;
`;
const BannerImg = styled.div`
  flex: 1;
  display: flex;
  justify-content: center;

  img {
    transform: scale(0.7);
  }
`;
const TextDiv = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  flex: 2;
  margin-left: 40px;
  color: rgba(32, 148, 0, 1);
  h1 {
    margin: 0;
    padding: 0;
    color: rgba(32, 148, 0, 1);
    font-size: 30px;
    font-weight: 600;
    width: 70%;
    margin-bottom: 20px;
    line-height: 60px;
    vertical-align: top;
  }
  h3 {
    margin: 0;
    padding: 0;
    color: rgba(0, 0, 0, 0.85);
    font-size: 18px;
    font-weight: 600;
    width: 70%;
    margin-bottom: 20px;
    line-height: 40.8px;
    letter-spacing: 4%;
    vertical-align: top;
  }
  h4 {
    margin: 0;
    padding: 0;
    color: rgba(32, 148, 0, 1);
    font-size: 18px;
    font-weight: 500;
    margin-top: 40px;
  }
`;

export default JoinOurTeam;
