import React from "react";
import styled from "styled-components";
import Navbar from "../../Components/Navbar/Navbar";
import Footer from "../../Components/Footer/Footer";
import { ReactComponent as FAQb } from "../../Images/FAQb.svg";
import MessageUs from "../../Components/MessageUs/MessageUs";
import FAQElement from "../../Components/FAQElement/FAQElement";
// import FAQb from "../../Images/FAQb.png";

const FAQs = () => {
  return (
    <Container>
      <Navbar active="Company" dropdownActive="FAQs" />
      <Body>
        <Banner>
          <BannerImg>
            {/* <img src={FAQb} alt="banner" /> */}
            <FAQb />
          </BannerImg>
        </Banner>
        <Section>
          <Title>
            <h1>
              <b>Giddy</b>Shop
            </h1>
          </Title>
          <FAQElement
            question="Whats the fastest way to reach us?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
          <FAQElement
            question="What are the service fee charges?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
          <FAQElement
            question="What countries do we mostly operate in?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
          <FAQElement
            question="Do you have an office?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
          <FAQElement
            question="Are my orders safe with GiddyShop?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
        </Section>
        <Section>
          <Title>
            <h1>
              <b>Giddy</b>Pay
            </h1>
          </Title>
          <FAQElement
            question="Are transactions safe with GiddyPay?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
          <FAQElement
            question="How long does transactions take?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
          <FAQElement
            question="Can trade all types of crypto & giftcards?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
          <FAQElement
            question="What are the service charges for transactions with GiddyPay?"
            answer="We give quick replies on all our socails but we are active 24/7 on our WhatsApp business account "
          />
        </Section>
        <Aside>
          <div>
            <Title>
              <h3>Still cant see your question?</h3>
            </Title>
            <MessageUs />
          </div>
        </Aside>
      </Body>
      <Footer />
    </Container>
  );
};
const Container = styled.div``;
const Body = styled.div`
  height: fit-content;
`;
const Banner = styled.div`
  max-width: 100vw;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(210, 234, 204, 1);
`;
const BannerImg = styled.div``;
const Title = styled.div`
  h1 {
    font-family: "Poppins", sans-serif;
    color: rgba(32, 148, 0, 1);
    margin: 0;
    padding: 0;
    font-size: 28px;
    font-weight: 500;
    text-align: left;
    margin-bottom: 20px;
  }
  h3 {
    font-size: 18px;
    font-weight: 500;
  }
`;
const Section = styled.div`
  margin-bottom: 100px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  width: 93%;
  display: flex;
  flex-direction: column;
  div {
    justify-content: center;
    align-items: center;
  }
`;
const Aside = styled.div`
  margin-bottom: 50px;
  margin-top: -40px;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;

  div {
    justify-content: center;
    align-items: center;
  }
`;

export default FAQs;
